// @todo enable the following disabled rules see OPENTOK-31136 for more info
/* eslint-disable no-underscore-dangle */

const assign = require('lodash/assign');
const guidStorage = require('./guid_storage.js');
const logging = require('../ot/logging.js');
const OTHelpers = require('../common-js-helpers/OTHelpers.js');
const otProperties = require('./otProperties.js');

module.exports = function Analytics(loggingUrl) {
  const LOG_VERSION = '2';
  const _analytics = new OTHelpers.Analytics(loggingUrl, logging.debug, guidStorage.get);

  this.logError = function (code, type, message, details, options = {}) {
    const partnerId = options.partnerId;

    guidStorage.get((error, guid) => {
      if (error) {
        // @todo
        return;
      }
      const data = assign({
        // TODO: properties.version only gives '2.2', not '2.2.9.3'.
        clientVersion: `js-${otProperties.version.replace('v', '')}`,
        buildHash: otProperties.buildHash,
        guid,
        partnerId,
        source: global.location && global.location.href,
        logVersion: LOG_VERSION,
        apiServer: otProperties.apiURL,
        clientSystemTime: new Date().getTime(),
      }, options);
      _analytics.logError(code, type, message, details, data);
    });
  };

  this.logEvent = function (options = {}, throttle, completionHandler) {
    const partnerId = options.partnerId;

    guidStorage.get((error, guid) => {
      if (error) {
        // @todo
        return;
      }

      // Set a bunch of defaults
      const data = assign({
        // TODO: properties.version only gives '2.2', not '2.2.9.3'.
        clientVersion: `js-${otProperties.version.replace('v', '')}`,
        buildHash: otProperties.buildHash,
        guid,
        partnerId,
        source: global.location && global.location.href,
        logVersion: LOG_VERSION,
        apiServer: otProperties.apiURL,
        clientSystemTime: new Date().getTime(),
      }, options);
      _analytics.logEvent(data, false, throttle, completionHandler);
    });
  };

  this.logQOS = function (options = {}) {
    const partnerId = options.partnerId;

    guidStorage.get((error, guid) => {
      if (error) {
        // @todo
        return;
      }

      // Set a bunch of defaults
      const data = assign({
        // TODO: properties.version only gives '2.2', not '2.2.9.3'.
        clientVersion: `js-${otProperties.version.replace('v', '')}`,
        buildHash: otProperties.buildHash,
        guid,
        partnerId,
        source: global.location && global.location.href,
        logVersion: LOG_VERSION,
        apiServer: otProperties.apiURL,
        clientSystemTime: new Date().getTime(),
        duration: 0, // in milliseconds
      }, options);

      _analytics.logQOS(data);
    });
  };
};



