// @todo enable the following disabled rules see OPENTOK-31136 for more info
/* eslint-disable no-shadow, no-void, global-require */

const defaults = require('lodash/defaults');

module.exports = function SessionInfoFactory(deps = {}) {
  const Anvil = deps.Anvil || require('../anvil.js')();
  const convertAnvilErrorCode = deps.convertAnvilErrorCode || require('../../helpers/convertAnvilErrorCode.js');
  const ExceptionCodes = deps.ExceptionCodes || require('../exception_codes.js');
  const logging = deps.logging || require('../logging.js');

  // This sequence defines the delay before retry. Therefore a 0 indicates
  // that a retry should happen immediately.
  //
  // e.g. 0, 600, 1200 means retry immediately, then in 600 ms, then in 1200ms
  //
  const retryDelays = [0, 600, 1200];

  // These codes are possibly transient and it's worth retrying if a Anvil request
  // fails with one of these codes.
  const transientErrorCodes = [
    ExceptionCodes.CONNECT_FAILED,
    ExceptionCodes.UNEXPECTED_SERVER_RESPONSE,
  ];

  const SessionInfo = function SessionInfo(rawSessionInfo) {
    logging.log('SessionInfo Response:', rawSessionInfo);

    /* jshint camelcase:false */
    // jscs:disable requireCamelCaseOrUpperCaseIdentifiers
    this.sessionId = rawSessionInfo.session_id;
    this.partnerId = rawSessionInfo.partner_id;
    this.messagingServer = rawSessionInfo.messaging_server_url;
    this.mediaServerName = rawSessionInfo.media_server_hostname;
    this.messagingURL = rawSessionInfo.messaging_url;
    this.symphonyAddress = rawSessionInfo.symphony_address;
    this.iceServers = rawSessionInfo.ice_servers;

    // `simulcast` is tri-state:
    // true: simulcast is on for this session
    // false: simulcast is off for this session
    // undefined: the developer can choose

    const properties = defaults(
      {},
      rawSessionInfo.properties || {},
      {
        reconnection: false,
        renegotiation: false,
        h264: true,
        vp9: true,
        vp8: true,
        rememberDeviceChoiceIE: false,
        priorityVideoCodec: '',
      }
    );

    this.simulcast = properties.simulcast;
    this.reconnection = properties.reconnection;
    this.renegotiation = properties.renegotiation;

    this.p2pEnabled = Boolean(properties.p2p &&
      properties.p2p.preference &&
      properties.p2p.preference.value === 'enabled');

    this.rememberDeviceChoiceIE = properties.rememberDeviceChoiceIE;
    this.priorityVideoCodec = properties.priorityVideoCodec;
    this.h264 = Boolean(properties.h264);
    this.vp9 = Boolean(properties.vp9);
    this.vp8 = Boolean(properties.vp8);

    this.clientCandidates = (properties.clientCandidates === 'relayed' ?
      'relay' :
      properties.clientCandidates
    );

    logging.log('SessionInfo parsed:', this);
  };

  // Retrieves Session Info for +session+. The SessionInfo object will be passed
  // to the +onSuccess+ callback. The +onFailure+ callback will be passed an error
  // object and the DOMEvent that relates to the error.
  //
  SessionInfo.get = (id, token, connectionId) => {
    const remainingRetryDelays = retryDelays.slice();

    const attempt = (err, resolve, reject) => {
      if (remainingRetryDelays.length === 0) {
        reject(err);
        return;
      }

      Anvil.get(`session/${id}?extended=true`, token, connectionId).then((anvilResponse) => {
        resolve(new SessionInfo(anvilResponse));
      }, (err) => {
        const normalisedCode = convertAnvilErrorCode(err.code);
        if (transientErrorCodes.indexOf(normalisedCode) > -1) {
          // This error is possibly transient, so retry
          setTimeout(() => {
            attempt(err, resolve, reject);
          }, remainingRetryDelays.shift());
        } else {
          reject(err);
        }
      });
    };

    return new Promise((resolve, reject) => {
      attempt(void 0, resolve, reject);
    });
  };

  return SessionInfo;
};



