// @todo enable the following disabled rules see OPENTOK-31136 for more info
/* eslint-disable no-underscore-dangle, one-var, no-use-before-define, no-plusplus */


// TODO: this is not unit tested

const clone = require('lodash/clone');
const assign = require('lodash/assign');
const analytics = require('../ot/analytics.js');
const OTHelpers = require('../common-js-helpers/OTHelpers.js');

module.exports = function ConnectivityAttemptPinger(options) {
  let _state = 'Initial';
  const states = ['Initial', 'Attempt', 'Success', 'Failure'];
  const PING_INTERVAL = 5000;
  const PING_COUNT_TOTAL = 6;

  let _previousState,
    pingTimer, // Timer for the Attempting ping;
    pingCount;

  // // Private API
  const stateChanged = function (newState) {
    _state = newState;
    let invalidSequence = false;
    switch (_state) {
      case 'Attempt':
        if (_previousState !== 'Initial') {
          invalidSequence = true;
        }
        startAttemptPings();
        break;
      case 'Success':
        if (_previousState !== 'Attempt') {
          invalidSequence = true;
        }
        stopAttemptPings();
        break;
      case 'Failure':
        if (_previousState !== 'Attempt') {
          invalidSequence = true;
        }
        stopAttemptPings();
        break;
      default:
    }
    if (invalidSequence) {
      const data = options ? clone(options) : {};
      data.action = 'Internal Error';
      data.variation = 'Non-fatal';
      data.payload = {
        debug: `Invalid sequence: ${options.action} ${
          _previousState} -> ${_state}`,
      };
      analytics.logEvent(data);
    }
  };

  const setState = OTHelpers.statable(this, states, 'Failure', stateChanged);

  let startAttemptPings = function () {
    pingCount = 0;
    pingTimer = setInterval(() => {
      if (pingCount < PING_COUNT_TOTAL) {
        const data = assign(options, { variation: 'Attempting' });
        analytics.logEvent(data);
      } else {
        stopAttemptPings();
      }
      pingCount++;
    }, PING_INTERVAL);
  };

  let stopAttemptPings = function () {
    clearInterval(pingTimer);
  };

  this.setVariation = function (variation) {
    _previousState = _state;
    setState(variation);

    // We could change the ConnectivityAttemptPinger to a ConnectivityAttemptLogger
    // that also logs events in addition to logging the ping ('Attempting') events.
    //
    // var payload = assign(options, {variation:variation});
    // analytics.logEvent(payload);
  };

  this.stop = function () {
    stopAttemptPings();
  };
};



