/* eslint-disable global-require */

const assert = require('assert');

const Errors = require('./Errors.js');
const ExceptionCodes = require('./exception_codes.js');

module.exports = function getUserMediaFactory(deps = {}) {
  ['processPubOptions'].forEach((key) => {
    assert(deps[key], `${key} dependency must be injected into OT.getUserMedia`);
  });

  const env = deps.env || require('../helpers/env.js');
  const logging = deps.logging || require('./logging.js');
  const otError = deps.otError || require('../helpers/otError.js')();
  const { processPubOptions } = deps;

  const getUserMedia = (pubOptions = {}) => {
    if (env.name === 'IE') {
      const error = otError(
        Errors.UNSUPPORTED_BROWSER,
        new Error('OT.getUserMedia is not supported with Internet Explorer'),
        ExceptionCodes.UNABLE_TO_PUBLISH
      );
      return Promise.reject(error);
    }

    const processedOptions = processPubOptions(pubOptions, 'OT.getUserMedia', () => false);
    return processedOptions.getUserMedia().catch((err) => {
      logging.error(`OT.getUserMedia failed to getUserMedia: ${err}`);
      throw err;
    });
  };

  return getUserMedia;
};



